.popup {
  display: flex;
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: rgba(255, 255, 255, 0.8);
  justify-content: center;
  cursor: zoom-out;
}

@media (orientation: landscape) {
  .popup {
    align-items: center;
  }
}
